var visit = require('unist-util-visit');

var emoji = require('node-emoji');

var emoticon = require('emoticon');

var RE_EMOJI = /:\+1:|:-1:|:[\w-]+:/g;
var RE_SHORT = /[$@|*'",;.=:\-)([\]\\/<>038BOopPsSdDxXzZ]{2,5}/g;
var DEFAULT_SETTINGS = {
  padSpaceAfter: false,
  emoticon: false
};

function plugin(options) {
  var settings = Object.assign({}, DEFAULT_SETTINGS, options);
  var pad = !!settings.padSpaceAfter;
  var emoticonEnable = !!settings.emoticon;

  function getEmojiByShortCode(match) {
    // find emoji by shortcode - full match or with-out last char as it could be from text e.g. :-),
    var iconFull = emoticon.find(function (e) {
      return e.emoticons.includes(match);
    }); // full match

    var iconPart = emoticon.find(function (e) {
      return e.emoticons.includes(match.slice(0, -1));
    }); // second search pattern

    var trimmedChar = iconPart ? match.slice(-1) : '';
    var addPad = pad ? ' ' : '';
    var icon = iconFull ? iconFull.emoji + addPad : iconPart && iconPart.emoji + addPad + trimmedChar;
    return icon || match;
  }

  function getEmoji(match) {
    var got = emoji.get(match);

    if (pad && got !== match) {
      return got + ' ';
    }

    return got;
  }

  function transformer(tree) {
    visit(tree, 'text', function (node) {
      node.value = node.value.replace(RE_EMOJI, getEmoji);

      if (emoticonEnable) {
        node.value = node.value.replace(RE_SHORT, getEmojiByShortCode);
      }
    });
  }

  return transformer;
}

module.exports = plugin;